.fight-container {
    display: flex;
    flex-direction: column;
    width: 500px;
    height: 500px;
    background-color: #ffe4c4;
    align-items: center;
    justify-content: center;
}

.img-container {
    width: 200px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.img-container img {
    width: 150px;
    height: 160px;
}

.fight {
    background-image: url('./20141124042846993.jpg');
    background-size: 100% 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    width: 400px;
    height: 200px;
    border-radius: 30px;
    box-shadow: 1px 1px 1px #73f1f1c4;
}

.HP-progress {
    background-color: #00000000;
}

.HP-progress::-webkit-progress-bar {
    width: 170px;
    height: 15px;
    border-radius: 8px;
    border: 1px solid black;
    background-color: rgb(114, 114, 114);
}

.HP-progress::-webkit-progress-value {
    width: 170px;
    height: 13px;
    border-radius: 8px;
    background-color: red;
}

.props-container {
    display: flex;
    flex-direction: row;
}

.props-plus {
    margin-left: 20px;
    height: 15px;
    border: 0;
    background-color: #397575c4;
    box-shadow: 2px 2px 2px #22222248;
}

.props-plus:hover {
    background-color: #73f1f1c4;
}

.atk {
    animation: atk 1s;
}

.props {
    width: 200px;
}

.enermy {
    transform: rotateY(180deg);
}

@keyframes atk {
    from {
        transform: translateX(0px);
    }
    50% {
        transform: translateX(70px);
    }
    100% {
        transform: translateX(0px);
    }
}